@import "../../../styles/variables";
@import "../../../styles/mixins";

@media print {

  @page {
    size: A4;
    margin: 13.49mm 19.5mm 13.49mm 19.5mm;
  }

  .previewLayout {
    header, 
    button {
      display: none;
    }
  }

  .pageInnerContainerFront {
    @include pageInside(ltr)
  }
  
  .card {
    @include card;
        
    .cardBody {
      @include cardBody;

      position: relative;

      small {
        position: absolute;
        padding: .5rem 1rem;
        text-align: end;
        bottom: 0;
        width: 100%;
        border-top: 1px solid #555;
      }
    }

    .withBorder {
      border: .5px dashed rgba(0,0,0, .1);
    }

    .cardFooter {
      @include cardFooter;
    }
  }

  .pageInnerContainerBack {
    @include pageInside(rtl)
  }
  
  .card {
    @include card;

    .cardBody {
      @include cardBody;
    }

    .cardFooter {
      @include cardFooter;
    }
  }
}

@media screen {
  .previewLayout {
    flex-grow: 1;
    display: flex;
    flex-flow: column nowrap;
    position: relative;

    .modalCloseBtn {
      position: absolute;
      top: 0;
      left: 0;
    }

    .previewBody {
      flex-grow: 1;
      display: flex;
      flex-flow: column nowrap;
      justify-content: space-between;
      .pageContainer {
        margin-top: 2em;
        height: 65vh;
        display: flex;
        flex-flow: column nowrap;
        justify-content: space-between;
        overflow-y: scroll;
        .pageInnerContainerFront,
        .pageInnerContainerBack {
          color: black;
          margin-top: 2em;
          background-color: white;
          height: 30vh;
          display: grid;
          grid-template-columns: repeat(2, 50%);
          grid-template-rows: repeat(5, 20%);
          .card {
            display: flex;
            flex-flow: column nowrap;
            justify-content: center;
            font-size: 8px;

            .cardBody {
            }
          }
        }

        .pageInnerContainerBack {
          direction: rtl;
        }
      }

      .previewControls {

      }
    }
  }
}